var token =""
export default {
	data() {
		return {
			  tabList:['我的邀请','排行榜'],
			  currentIndex:0,
			  number:0,
			  myInList:[
						],
			  inNoList:[{
						avatar: "http://pic4.zhimg.com/50/v2-2ef01343920e66f878b05ff380d902d7_hd.jpg",
						id: "string",
						nickname: "张三",
						number: 991
						},{
						avatar: "http://pic4.zhimg.com/50/v2-2ef01343920e66f878b05ff380d902d7_hd.jpg",
						id: "string",
						nickname: "李四",
						number: 9
						},{
						avatar: "http://pic4.zhimg.com/50/v2-2ef01343920e66f878b05ff380d902d7_hd.jpg",
						id: "string",
						nickname: "王五",
						number: 88
						},{
						avatar: "http://pic4.zhimg.com/50/v2-2ef01343920e66f878b05ff380d902d7_hd.jpg",
						id: "string",
						nickname: "小明",
						number: 91
						}]
		}
	},
	onLoad() {
		token = uni.getStorageSync("token")
		
		this.loadMyInvitaData()
		this.loadRankData()
	},
	methods: {
		tabBtn(index)
		{
			console.log(index)
		    this.currentIndex=index;
		},
		//获取我的邀请数据
		loadMyInvitaData()
		{
			var _this =this
			uni.request({
			    url:_this.$url+'/user/invite/my' ,//服务器端地址
			    data: {
					pageNum: 0,
					pageSize: 100
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.myInList = res.data.data.content
						_this.number = _this.myInList.length
			        }
			    }
			   
			});
		},
		//获取我的邀请数据
		loadRankData()
		{
			var _this =this
			uni.request({
			    url:_this.$url+'/user/invite/ranking' ,//服务器端地址
			    data: {
					pageNum: 0,
					pageSize: 100
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.inNoList = res.data.data.content
			        }
			    }
			   
			});
		},
		nav()
		{
			uni.navigateTo({
				url:"../showinvita/showinvita"
			})
		},
		
	}
}
